<template>
	<div id="Music">
		<div class="top">
			<div class="left">
				<div class="music">
					<div class="pic"><img src="static/img/ic_my_doulist.png" /></div>
					<p class="one">找音乐</p>
					<p class="two">欧美/韩国/民谣</p>
					<span>></span>
				</div>
			</div>
			<div class="right">
				<div class="my">
					<div class="pic"><img src="static/img/ic_photo_liked.png" /></div>
					<p class="one">我的音乐</p>
					<p class="two">未登录</p>
					<span>></span>
				</div>
			</div>
		</div>
		<div class="content">
			<p class="one">华语新碟榜</p>
			<p class="two">更多<i>></i></p>
			<div class="swiper-container">
			    <div class="swiper-wrapper">
			        <div class="swiper-slide" v-for="(item,index) in list">
			        	<img :src='item.subject.images.small'>
			        	<p class="p-one">{{item.subject.title}}</p>
			        	<p class="p-four">{{item.subject.name}}</p>
			        	<p class="p-two">评分:</p>
			        	<p class="p-three">{{item.subject.rating.average}}</p>
			        </div>
			    </div>
			</div>
		</div>
		<div class="content">
			<p class="one">华语金曲</p>
			<p class="two">更多<i>></i></p>
			<div class="swiper-container">
			    <div class="swiper-wrapper">
			        <div class="swiper-slide" v-for="(item,index) in list1">
			        	<img :src='item.subject.images.small'>
			        	<p class="p-one">{{item.subject.title}}</p>
			        	<p class="p-four">{{item.subject.name}}</p>
			        	<p class="p-two">评分:</p>
			        	<p class="p-three">{{item.subject.rating.average}}</p>
			        </div>
			    </div>
			</div>
		</div>
		<div class="content">
			<p class="one">热门单曲</p>
			<p class="two">更多<i>></i></p>
			<div class="swiper-container">
			    <div class="swiper-wrapper">
			        <div class="swiper-slide" v-for="(item,index) in list2">
			        	<img :src='item.subject.images.small'>
			        	<p class="p-one">{{item.subject.title}}</p>
			        	<p class="p-four">{{item.subject.name}}</p>
			        	<p class="p-two">评分:</p>
			        	<p class="p-three">{{item.subject.rating.average}}</p>
			        </div>
			    </div>
			</div>
		</div>
		<div class="content">
			<p class="one">KTV麦榜</p>
			<p class="two">更多<i>></i></p>
			<div class="swiper-container">
			    <div class="swiper-wrapper">
			        <div class="swiper-slide" v-for="(item,index) in list3">
			        	<img :src='item.subject.images.small'>
			        	<p class="p-one">{{item.subject.title}}</p>
			        	<p class="p-four">{{item.subject.name}}</p>
			        	<p class="p-two">评分:</p>
			        	<p class="p-three">{{item.subject.rating.average}}</p>
			        </div>
			    </div>
			</div>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import Axios from 'axios'
	import VueAxios from 'vue-axios'
	Vue.use(VueAxios,Axios)
	export default{
		data:function(){
			return{
				list:'',
				list1:'',
				list2:'',
				list3:'',
			}
		},
		created:function(){
			this.axios.get('static/musicData1.json').then(
				(res)=>{
					this.list=res.data.subjects;
					this.list1=res.data.subjects1;
					this.list2=res.data.subjects2;
					this.list3=res.data.subjects3;
//					console.log(res.data.subjects)
					console.log(this.list[0])
				}
			)
		},
		updated:function(){
			var mySwiper = new Swiper('.swiper-container',{
				slidesPerView : 3.5,
				spaceBetween : 15,
				freeMode:true,
			})
		}
	}
</script>

<style>
	#Music{margin: 36px 0 50px 0;}
	#Music .top{width: 100%;height: 86px;border-bottom: 1px solid #CDCDCD;}
	#Music .left{float: left;width: 60%;}
	#Music .left .music{border-right: 1px solid #cdcdcd;height: 50px;margin-top: 20px;position: relative;}
	#Music .left .music .pic{height: 50px;width: 50px;font-size: 0;border-radius: 50px;}
	#Music .left .music .pic img{height: 50px;width: 50px;}
	#Music .left .music .one{position: absolute;top: 6px;left:55px;font-size: 14px;font-weight: bold;}
	#Music .left .music .two{position: absolute;top: 25px;left:55px;font-size: 12px;color: #CDCDCD;}
	#Music .left .music span{position: absolute;top: 10px;right: 15px;color: #CDCDCD;}
	#Music .right{float: right;width: 40%;}
	#Music .right .my{height: 50px;margin-top: 20px;position: relative;}
	#Music .right .my .pic{height: 50px;width: 50px;font-size: 0;border-radius: 50px;}
	#Music .right .my .pic img{height: 50px;width: 50px;}
	#Music .right .my .one{position: absolute;top: 6px;left:55px;font-size: 14px;font-weight: bold;}
	#Music .right .my .two{position: absolute;top: 25px;left:55px;font-size: 12px;color: #CDCDCD;}
	#Music .right .my span{position: absolute;top: 10px;right: 10px;color: #CDCDCD;}
	#Music .content{height: 210px;width: 100%;position: relative;}
	#Music .content .one{margin-left: 15px;margin-top: 15px;font-size: 18px;font-weight: bold;color: #393939;font-family: "microsoft yahei";}
	#Music .content .two{position: absolute;right: 15px;top: 0px;font-size: 14px;color: #42BF55;}
	#Music .content .swiper-container{height: 170px;padding-top:10px;margin-left: 15px;}
	#Music .content .swiper-slide img{width: 100px;height: 100px;}
	#Music .content .swiper-slide .p-one{position: absolute;top: 100px;font-size: 14px;color: #474747;height: 30px;line-height: 30px;}
	#Music .content .swiper-slide .p-four{position: absolute;top: 117px;font-size: 12px;color: sandybrown;height: 30px;line-height: 30px;}
	#Music .content .swiper-slide .p-two{position: absolute;top: 142px;font-size: 12px;color: salmon;}
	#Music .content .swiper-slide .p-three{position: absolute;top: 142px;left:35px;font-size: 12px;color: salmon;}
</style>


