<template>
	<div class="homeWrap">
		<myHead class="header">
			<input type="text" placeholder="影视 图书 唱片 小组等" slot="input"/>
			<img class="homesearch" :src="src1" alt="" slot="title"/>
			<img class="homescan" src="./img/ic_scan_gray.png" alt="" slot="left"/>
			<img class="chat" src="./img/ic_chat_white.png" alt="" slot="right"/>
		</myHead>
		<Banner></Banner>
		<div class="redd">
			<span>热点</span>
		</div>
		<HomeList></HomeList>
	</div>
</template>

<script>
	import myHead from '../head'
	import Banner from './banner'
	import HomeList from './homeList'
	export default{
		data:function(){
			return {
				src1:'/static/img/ic_search.png'
			}
		},
		methods:{},
		components:{
			myHead,
			Banner,
			HomeList
		}
	}
</script>

<style>
	.homeWrap .home{background-color: #42bf55;}
	.homeWrap .header{position: fixed;top: 0;z-index: 100;}
	.redd{position: relative;height: 30px;border-left: 5px solid #fa8535;}
	.redd span{color:#f77750;position: absolute;left: 16px;height: 30px;line-height: 30px;top: -10px;}
</style>


