<template>
	<div id="Broadcastlist">
		<div class="dec">
			<img src="../img/ic_coupon_movie.png" class="pic"/>
			<span class="text">发现有趣的人</span>
			<span class="text1">关注他们，发现更大的世界</span>
		</div>
		<div class="b-content">
				<ul>
					<li v-for="(item,index) in list">
						<div class="one"><img :src="item.userPic"></div>
						<p class="two">{{item.userName}}</p>
						<p class="three">{{item.userId}}人关注</p>
						<div class="four" @click="close(index)"><img src="../img/ic_close_status_recommend.png"></div>
						<p class="five" @click="attention(index)">{{item.pay}}</p>
						<p class="six">{{item.commentTxt}}</p>
						<div class="seven" @click="like(index)"><img src="../img/ic_liked.png"></div>
						<p class="eight">{{item.praiseNum}}</p>
						<div class="nine"><img src="../img/ic_comment_full.png"></div>
						<p class="ten">{{item.replyNum}}</p>
					</li>
				</ul>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import Axios from 'axios'
	import VueAxios from 'vue-axios'
	Vue.use(VueAxios,Axios)
	export default{
		data:function(){
			return{list:'',}
		},
		created:function(){
			this.axios.get('static/broadcastData.json').then(
				(res)=>{
					this.list=res.data.data.recommend_feeds;
				}
			)
		},
		methods:{
			close:function(i){
				this.list.splice(i,1);
			},
			attention:function(i){
				document.getElementsByClassName("five")[i].style.backgroundColor="darkgray";
			},
			like:function(i){
				this.list[i].praiseNum++;
			},
		},
	}
</script>

<style>
	#Broadcastlist{background: #f7f7f7;width: 100%;height: 100%;}
	#Broadcastlist .dec{height: 107px;}
	#Broadcastlist .pic{margin: 10px 0 0 78px;float: left;}
	#Broadcastlist .text{margin: 28px 0 0 10px;float: left;font-weight: bold;font-size: 16px;}
	#Broadcastlist .text1{margin: 2px 0 0 10px;float: left;color: #939393;font-size: 14px;}
	#Broadcastlist .b-content li{margin: 0 15px 10px;background: #fff;border-radius: 10px;position: relative;height: 300px;}
	#Broadcastlist .b-content .one{position: absolute;top: 20px;left: 10px;}
	#Broadcastlist .b-content .one img{height: 60px;width: 60px;border-radius: 50px;}
	#Broadcastlist .b-content .two{position: absolute;top: 20px;left: 80px;font-size: 20px;font-weight:550;color: #646464;}
	#Broadcastlist .b-content .three{position: absolute;top: 50px;left: 80px;font-size: 14px;color: #cdcdcd;}
	#Broadcastlist .b-content .four{position: absolute;top: 5px;right: 5px;}
	#Broadcastlist .b-content .four img{height: 30px;width: 30px;}
	#Broadcastlist .b-content .five{position: absolute;background: #45bd5b;width: 70px;height: 30px;border-radius: 4px;text-align: center;line-height: 30px;color: #fff;top: 35px;right: 35px;}
	#Broadcastlist .b-content .six{position: absolute;font-size: 16px;color:  #646464;top: 80px;left: 80px;width: 69%;overflow: hidden;height: 56%;}
	#Broadcastlist .b-content .seven{position: absolute;top: 260px;left: 80px;}
	#Broadcastlist .b-content .seven img{width: 25px;height: 25px;}
	#Broadcastlist .b-content .eight{position: absolute;top: 265px;left: 110px;font-size: 14px;color: #dcdcdc;}
	#Broadcastlist .b-content .nine{position: absolute;top: 265px;left: 145px;}
	#Broadcastlist .b-content .nine img{width: 18px;height: 18px;}
	#Broadcastlist .b-content .ten{position: absolute;top: 265px;left: 175px;font-size: 14px;color: #dcdcdc;}
	#Broadcastlist .ac{background: darkgray;}
</style>


