<template>
	<div class="hehe" @click="change">
		<span v-show="bool"><slot name="normal"></slot><br/></span>
		<span v-show="!bool"><slot name="active"></slot><br/></span>
		<span class="title" :class="{active:!bool}">{{title}}</span>
	</div>

</template>

<script>
	export default{
		data:function(){
			return {}
		},
		props:['mark','sel','title'],
		methods:{
			change:function(){
				this.$emit("send",this.mark)
				this.$router.push('/'+this.mark)
			}
		},
		computed:{
			bool:function(){
				if(this.mark==this.sel){
					return false
				}else{
					return true
				}
			}
		}
	}
</script>

<style>

</style>


