<template>
	<div class="tabbarWrap">
		    <item :title="'首页'" mark='home' :sel="selector" @send="getValue">      
	    		<img src="./img/ic_tab_home_normal.png" alt="" slot="normal"/>
				<img src="./img/ic_tab_home_active.png" alt="" slot="active"/>
	    	</item>
	    	<item :title="'书影音'" mark='book' :sel="selector" @send="getValue"> 
	    		<img src="./img/ic_tab_subject_normal.png" alt="" slot="normal"/>
				<img src="./img/ic_tab_subject_active.png" alt="" slot="active"/>
	    	</item>
	    	<item :title="'广播'" mark='broadCast' :sel="selector" @send="getValue">
	    		<img src="./img/ic_tab_group_normal.png" alt="" slot="normal"/>
	    		<img src="./img/ic_tab_group_active.png" alt="" slot="active"/>
	    	</item>
	    	<item :title="'小组'" mark='group' :sel="selector" @send="getValue">
	    		<img src="./img/ic_tab_status_normal.png" alt="" slot="normal"/>
				<img src="./img/ic_tab_status_active.png" alt="" slot="active"/>
	    	</item>
	    	<item :title="'我的'" mark='personal' :sel="selector" @send="getValue">
	    		<img src="./img/ic_tab_profile_female_normal.png" alt="" slot="normal"/>
	    		<img src="./img/ic_tab_profile_female_active.png" alt="" slot="active"/>
	    	</item>
	</div>
</template>

<script>
	import item from './tabbarChild'
	export default{
		data:function(){
			return {
				selector:'home'
			}
		},
		methods:{
			getValue:function(n){
				this.selector=n
			}
		},
		components:{
			item
		}
	}
</script>

<style>
	.tabbarWrap{width: 100%;height: 64px;background-color: #F6F6F6;border-top: 1px solid #B5B5B5;position: fixed;left: 0;bottom: 0;z-index: 1000;}
    .tabbarWrap img{width: 45px;}
    .tabbarWrap .hehe{width: 20%;float: left;text-align: center;font-size: 0;}
    .tabbarWrap .title{font-size: 14px;color: #8B8B8B;}
    .hehe .active{color: #42bf55;}
</style>


