<template>
	<div id="Movie">
		<div class="de">
			<div class="top">
				<p class="one">更多></p>
				<p class="two">影院热映</p>
				<div class="swiper-container">
				    <div class="swiper-wrapper">
				        <div class="swiper-slide" v-for="(item,index) in list">
				        	<img :src='item.images.small'>
				        	<p class="p-one">{{item.title}}</p>
				        	<p class="p-two">评分:</p>
				        	<p class="p-three">{{item.rating.average}}</p>
				        </div>
				    </div>
				</div>
			</div>
			
		</div>
		<div class="concent">
			<div class="mid">
				<div class="pic2"><img src="./img/02.jpg"></div>
				<p>“康熙合体”，小s放飞自我，白眼怒怼恶评</p>
			</div>
		</div>
		<div class="futher">
			<div class="top">
				<p class="one">更多></p>
				<p class="two">院线即将上映</p>
				<div class="swiper-container">
				    <div class="swiper-wrapper">
				        <div class="swiper-slide" v-for="(item,index) in list1">
				        	<img :src='item.images.small'>
				        	<p class="p-one">{{item.title}}</p>
				        	<p class="p-two">评分:</p>
				        	<p class="p-three">{{item.rating.average}}</p>
				        </div>
				    </div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Vue from 'vue'
	import Axios from 'axios'
	import VueAxios from 'vue-axios'
	Vue.use(VueAxios,Axios)
	export default{
		data:function(){
			return{
				list:'',
				list1:'',
			}
		},
		created:function(){
			this.axios.get('static/movieData.json').then(
				(res)=>{
					this.list=res.data.subjects;
//					console.log(res.data)
				}
			),
			this.axios.get('static/moviefuterData.json').then(
				(res)=>{
					this.list1=res.data.movies;
				}
			)
		},
		updated:function(){
			var mySwiper = new Swiper('.swiper-container',{
				slidesPerView : 3.5,
				spaceBetween : 15,
				freeMode:true,
			})
		}
	}
	
</script>

<style>
	#Movie{width: 100%;margin: 31px 0 28px 0;}
	#Movie .de{height: 270px;position: relative;background: #f7f7f7;}
	#Movie .de .top{background: #fff;width: 100%;position: absolute;top: 10px;height: 240px;}
	#Movie .de .top .one{float: right;margin-right: 15px;margin-top: 20px;font-size: 14px;color: limegreen;}
	#Movie .de .top .two{margin-left: 15px;margin-top: 15px;font-size: 18px;font-weight: bold;}
	#Movie .concent{position: relative;height: 190px;background: #f7f7f7;}
	#Movie .concent .mid{width: 100%;position: absolute;top: 0px;height: 170px;background: #fff;}
	#Movie .concent .mid img{width: 100%;}
	#Movie .concent .mid p{text-align: center;height: 30px;line-height: 30px;}
	#Movie .de .top .swiper-container{height: 170px;padding-top: 15px;margin-left: 15px;}
	#Movie .de .top .swiper-slide img{width: 100px;height: 130px;}
	#Movie .de .top .swiper-slide .p-one{position: absolute;top: 132px;font-size: 14px;color: #474747;height: 30px;line-height: 30px;}
	#Movie .de .top .swiper-slide .p-two{position: absolute;top: 156px;font-size: 12px;color: #9a9a9a;}
	#Movie .de .top .swiper-slide .p-three{position: absolute;top: 156px;left:35px;font-size: 12px;color: #9a9a9a;}
	#Movie .futher{height: 290px;position: relative;background: #f7f7f7;}
	#Movie .futher .top{background: #fff;width: 100%;position: absolute;height: 240px;}
	#Movie .futher .top .one{float: right;margin-right: 15px;margin-top: 20px;font-size: 14px;color: limegreen;}
	#Movie .futher .top .two{margin-left: 15px;margin-top: 15px;font-size: 18px;font-weight: bold;}
	#Movie .futher .top .swiper-container{height: 170px;padding-top: 15px;margin-left: 15px;}
	#Movie .futher .top .swiper-slide img{width: 100px;height: 130px;}
	#Movie .futher .top .swiper-slide .p-one{position: absolute;top: 132px;font-size: 14px;color: #474747;height: 30px;line-height: 30px;}
	#Movie .futher .top .swiper-slide .p-two{position: absolute;top: 156px;font-size: 12px;color: #9a9a9a;}
	#Movie .futher .top .swiper-slide .p-three{position: absolute;top: 156px;left:35px;font-size: 12px;color: #9a9a9a;}
</style>


