<template>
	<div>
		<myHead class="gheader">
			<span slot="input">小组</span>
			<img class="search" src="../img/ic_group_search.png" alt="" slot="left"/>
			<img class="chat" src="../img/ic_chat_green.png" alt="" slot="right"/>
		</myHead>
		<Banner></Banner>
		<GroupList></GroupList>
	</div>
</template>

<script>
	import myHead from '../head'
	import Banner from './banner'
	import GroupList from './groupList'
	export default{
		data:function(){
			return {}
		},
		methods:{},
		components:{
			myHead,
			Banner,
			GroupList
		}
	}
</script>

<style>
	.gheader{position: fixed;top: 0;left: 0;z-index: 100;}
</style>


