<template>
	<div>
		<ul>
			<li v-for="(item,index) in list">
				<div class="box">
					<h2>{{item.title}}</h2>
					<span class="des">{{item.target.desc}}</span>
					<span class="author">{{item.target.author.name}}</span>
					<span class="txt">{{item.source_cn}}</span>
					<img :src="item.target.author.avatar" alt="" />
				</div>
			</li>
		</ul>
	</div>
</template>

<script>
	import Vue from 'vue'
	import Axios from 'axios'
	import VueAxios from 'vue-axios'
	Vue.use(VueAxios,Axios)
	export default{
		data:function(){
			return {list:''}
		},
		created:function(){
			this.axios.get('./static/homeData.json').then(
				(res)=>{
					console.log(res.data.recommend_feeds);
					this.list=res.data.recommend_feeds;
				}
			)
		}
	}
</script>

<style>
	*{margin: 0;padding: 0;}
	ul{list-style: none;}
	.box{width: 95%;height: 180px;position: relative;border-bottom: 1px solid #e3e3e3;}
	.box img{width: 86px;height: 86px;position: absolute;top: 18px;right: 18px;}
	.box h2{position: absolute;left: 20px;right: 120px;top: 20px;line-height: 24px;font-size: 18px;}
	.box span{color: #D1D1D1;font-size: 12px;}
	.box .des{position: absolute;line-height: 17px;left: 20px;right: 150px;top: 90px;color: #a4a4a4;height: 68px;overflow: hidden;}
	.box .author{position: absolute;left: 20px;bottom: 0px;}
	.box .txt{position: absolute;line-height: 17px;right: 20px;bottom:17px;}
</style>


