<template>
	<div>
		<myHead class="gheader">
			<span slot="input">书影音</span>
			<img class="search" src="/static/img/ic_group_search.png" alt="" slot="left"/>
			<img class="chat" src="/static/img/ic_chat_green.png" alt="" slot="right"/>
		</myHead>
		<div class="bookwrap">
			<ul class="nav">   <!--此时要给ul单独起名字，否则router-view会继承所有ul样式-->
				<li :class="{active:nowIndex==1}" @click="nowIndex=1"><router-link to='/book/video'>电影</router-link></li>
				<li :class="{active:nowIndex==2}" @click="nowIndex=2"><router-link to="/book/read">读书</router-link></li>
				<li :class="{active:nowIndex==3}" @click="nowIndex=3"><router-link to="/book/tv">电视</router-link></li>
				<li :class="{active:nowIndex==4}" @click="nowIndex=4"><router-link to="/book/city">同城</router-link></li>
				<li :class="{active:nowIndex==5}" @click="nowIndex=5"><router-link to="/book/music">音乐</router-link></li>
			</ul>
		</div>
		<router-view></router-view>
	</div>
	
</template>

<script>
	import myHead from '../head'
	export default{
		data:function(){
			return {
				nowIndex:1    //刷新时默认重置为第一个
			}
		},
		methods:{},
		components:{
			myHead,
		},
		created:function(){
  			this.$router.push('/book/video')
  		}
	}
</script>

<style>
	.gheader{position: fixed;top: 0;left: 0;z-index: 100;}
	.bookwrap{padding-top: 50px;}
	.bookwrap .nav {list-style: none;border-bottom: 1px solid #e5e5e5;height: 38px;display: flex;justify-content: space-between;flex-direction: row;text-align: center;position: fixed;top: 51px;left: 0; width: 100%;background-color: #FFFFFF;z-index: 1000;}
	.bookwrap .nav li{font-size: 16px;height: 36px;line-height: 36px;flex-grow: 1;}
	.bookwrap .active a{color:#64c780;}   /*点击时a的颜色*/
	.bookwrap a{text-decoration: none;font-size: 16px;color: #919191;}
	.bookwrap .active{border-bottom: 3px solid #64c780;}
</style>


