<template>
	<div class="swiper-container">
	    <div class="swiper-wrapper">
	        <div class="swiper-slide slide1"><img src="./banner-img/01.jpg" alt="" /></div>
	        <div class="swiper-slide slide2"><img src="./banner-img/02.jpg" alt="" /></div>
	        <div class="swiper-slide slide3"><img src="./banner-img/03.jpg" alt="" /></div>
	    </div>
	    <!-- 如果需要分页器 -->
	 	<div class="swiper-pagination"></div>
	</div>
	
</template>

<script>
	export default{
		mounted:function(){   //钩子函数，挂载完成
			new Swiper ('.swiper-container', {
		    direction: 'horizontal',
		    loop: true,
		    autoplay:1000,
		    pagination: '.swiper-pagination',
		  })    
		}
	}
</script>

<style scoped="scoped">
	.swiper-container{
		width: 100%;
		margin-top: 51px;
	}
	.swiper-container img{
		width: 100%;
	}
</style>


