<template>
	<div class="Readboxs">
		<img src="./img/b7.jpg"  class="pic"/>
		<div class="head">
			<h3>新书速递</h3>
			<a href="#"><span>更多》</span>	</a>
		</div>
		<div>
			<ul class="book">
				<li>
					<img src="./img/b1.jpg"/>
					<h5>刺</h5>
					<p>李尚龙</p>
				</li>
				<li>
					<img src="./img/b2.jpg"/>
					<h5>兄弟</h5>
					<p>余华</p>
				</li>
				<li>
					<img src="./img/b3.jpg"/>
					<h5>黑马侦探5：阿马里洛</h5>
					<p>[西]胡安-丽亚</p>
				</li>
				<li>
					<img src="./img/b4.jpg"/>
					<h5>看见鲸鱼座的人</h5>
					<p>糖菲</p>
				</li>
				<li>
					<img src="./img/b5.jpg"/>
					<h5>虚无的质感</h5>
					<p>梅雪峰</p>
				</li>
				<li>
					<img src="./img/b6.jpg"/>
					<h5>53天</h5>
					<p>[法]乔治-佩兰克</p>
				</li>
			</ul>
		</div>
		<BookList></BookList>
	</div>
</template>

<script>
	import BookList from './bookList'

	export default{
		data:function(){
			return {
				nowIndex:1    //刷新时默认重置为第一个
			}
		},
		methods:{},
		components:{
			BookList
		},
	}
</script>

<style scoped="scoped">
	a{text-decoration: none;}
	.Readboxs{padding: 0 3%;padding-bottom: 60px;margin-top: 56px}
	.Readboxs .pic{width: 100%;}
	.Readboxs .head{display: flex;border-bottom: 1px solid seagreen;width: 100%;flex-direction: row;justify-content: space-between;}
	.Readboxs .head h3{line-height: 30px;height: 30px;}
	.Readboxs .head span{line-height: 30px;height: 30px;color: #63a778;}
	.Readboxs .book{display: flex;justify-content: space-around;flex-direction: row;flex-wrap: wrap;text-align: center;}
	.Readboxs .book li{padding-bottom: 10px;width: 32%;}
	.Readboxs .book img{width: 115px;height: 172px;}
	.Readboxs .book h5{color: slateblue;font-size: 15px;}
	.Readboxs .book p{color: seagreen;}
</style>


